import axios from "axios";
import React, { useState, useEffect, useRef } from "react";
import { styled } from "styled-components";
import markerIcon from "../assets/image/marker.png";
import backIcon from "../assets/image/leftArrowIcon.png";
import searchIcon from "../assets/image/searchIcon.png";

const AddSchedulePlace = () => {
  const { naver } = window;
  const [themeCategorySelect, setThemeCategorySelect] = useState("전국");
  const [themeCategoryList, setThemeCategoryList] = useState([]);

  // 주변마커 오버레이 클릭 이벤트 적용하기
  useEffect(() => {
    getData();
    new naver.maps.Map("map", {
      center: new naver.maps.LatLng(36.5, 127.6),
      zoomControl: false,
      zoom: 6,
    });
  }, []);

  const getData = async () => {
    await axios({
      method: "get",
      url: `${process.env.REACT_APP_API_URL}/v2/place/category`,
    })
      .then(async (res) => {
        console.log(res.data);
        setThemeCategoryList(res.data.themeList);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <Container>
      <HeaderContainer>
        <HeaderBackBtn src={backIcon} />
        <HeaderTextArea>장소 추가</HeaderTextArea>
        <HeaderSearchBtn src={searchIcon} />
      </HeaderContainer>
      <ScrollCategoryContainer>
        {themeCategoryList.map((el, idx) => {
          return <CategoryBtn key={idx}>{el}</CategoryBtn>;
        })}
      </ScrollCategoryContainer>
      <div id="map" style={{ width: "100%", display: "flex", flex: 1 }} />
      <ConfirmContainer>
        <ConfirmBtn>일정 추가 완료</ConfirmBtn>
      </ConfirmContainer>
    </Container>
  );
};

